@use "./breakpoints" as *;
@use "./colors" as *;
@use "./buttons" as *;
@use "./sectionmarkers" as *;
@use "./cards" as *;

@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap");

*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  margin: 0;
  padding: 0;
  border: 0;
}

html,
body,
div,
span,
object,
iframe,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
code,
em,
img,
small,
strike,
strong,
sub,
sup,
tt,
b,
u,
i,
ol,
ul,
li,
fieldset,
form,
label,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
main,
canvas,
embed,
footer,
header,
nav,
section,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

body {
  background: $white;
  color: $gray;
  line-height: 1;
  font-family: "Red Hat Display", sans-serif;
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 1.625rem;
  overflow-x: hidden;
}

header {
  overflow: hidden;

  nav {
    #logo {
      padding: 3rem 0 0 0;
      text-align: center;

      @include lg {
        padding: 4rem 0 0 0;
      }

      @include xl {
        padding: 5rem 0 0 0;
      }
    }

    #header-container {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      @include lg {
        flex-direction: row;
      }

      #header-images {
        display: flex;
        width: 111%;
        margin: 0 0 0 -1.35rem;
        padding: 0;

        @include md {
          width: 106.5%;
          margin: 0 0 0 -1.56rem;

          @include lg {
            display: none;
          }

          img {
            width: 100%;
          }
        }

        img {
          width: 100%;
          height: 100%;
          padding: 3.375rem 0 3rem 0;

          @include md {
            padding: 4rem 0 4.5rem;
          }
        }
      }

      #image-hero-left,
      #image-hero-right {
        display: none;

        @include lg {
          display: flex;
          padding: 0;

          img {
            object-fit: contain;
          }
        }
      }

      #image-hero-left {
        margin: 0 0 0 -2.1rem;
      }

      #image-hero-right {
        margin: 7rem -1.85rem 0 0;
      }

      #header-text {
        h1 {
          font-size: 2.5rem;
          font-weight: 900;
          line-height: 2.75rem;
          text-align: center;
          color: $dark-blue;
          padding: 0 1.5rem;

          @include md {
            padding: 0 14rem;
          }

          @include lg {
            padding: 5rem 4rem 0;
            line-height: 3.25rem;
            font-size: 2.75rem;
          }

          @include xl {
            padding: 6.75rem 6.3rem 0;
            line-height: 4rem;
            font-size: 4rem;
          }
        }

        p {
          font-size: 1rem;
          line-height: 1.625rem;
          text-align: center;
          color: $gray;
          padding: 1.5rem 0 2rem 0;

          @include md {
            padding: 1.5rem 9rem 2rem;
          }

          @include lg {
            padding: 1.8rem 3rem 2.5rem;
          }

          @include xl {
            padding: 2.125rem 4.5rem 3.5rem;
            line-height: 1.625rem;
            font-size: 1.125rem;
          }
        }
        .cta-container {
          display: flex;
          flex-direction: column;
          text-align: center;
          align-items: center;

          @include md {
            flex-direction: row;
            justify-content: center;
            gap: 1rem;
          }

          button {
            @include btn-style();
          }

          button:nth-child(1) {
            width: 12.063rem;
            color: $white;
            background: $teal;

            &:hover {
              background-color: $light-teal;
            }

            @include md {
              margin: 0;
            }

            span {
              color: $light-blue;
            }
          }

          button:nth-child(2) {
            width: 8.688rem;
            color: $white;
            background: $dark-purple;
            margin: 0 0 4rem 0;

            &:hover {
              background-color: $medium-purple;
            }

            @include md {
              margin: 0;
            }
          }
        }
      }
    }

    div {
      padding: 0 1.5rem;
    }

    #section-marker-1 {
      @include section-marker;

      @include md {
        padding: 5rem 0 4rem 0;
      }
    }
  }
}

main {
  margin: 0 auto;

  .flex-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    padding: 1.5rem;

    @include md {
      flex-direction: row;
      padding: 1.5rem 2.5rem 2.25rem 2.438rem;
    }

    @include lg {
      padding: 0 1.75rem 4rem 2rem;
      justify-content: center;
      gap: 1.5rem;
    }

    @include xl {
      padding: 0 2.5rem 5rem 2.438rem;
      gap: 1.875rem;
    }

    .card {
      display: flex;
      gap: 1.563rem;

      @include md {
        gap: 0.688rem;
      }

      @include lg {
        gap: 1.5rem;
      }

      @include xl {
        gap: 1.875rem;
      }

      img {
        width: 100%;
        height: 8.938rem;
        max-height: 100%;
        border-radius: 0.5rem;
        object-fit: cover;

        @include md {
          height: 9.75rem;
        }

        @include lg {
          height: 12rem;
        }

        @include xl {
          height: 15.125rem;
        }
      }
    }
  }

  #image-text {
    @include card-text;

    p:nth-child(1) {
      color: $teal;
      font-size: 1rem;
      line-height: 1.625rem;
      font-weight: 900;
      letter-spacing: 0.25rem;
      text-transform: uppercase;
    }

    p:nth-child(2) {
      color: $dark-blue;
      font-size: 2rem;
      line-height: 2.25rem;
      font-weight: 900;
      padding: 1.5rem 0 2rem;

      @include md {
        font-size: 2.5rem;
        line-height: 2.25rem;
        padding: 1.25rem 9rem 2rem;
      }

      @include lg {
        padding: 1.25rem 24rem 2rem;
      }

      @include xl {
        padding: 1.25rem 30rem 2rem;
      }
    }

    p:nth-child(3) {
      color: $gray;
      font-size: 1rem;
      line-height: 1.625rem;
      font-weight: 500;
      padding: 0 0 4rem;

      @include md {
        padding: 0 4rem 5rem;
      }

      @include lg {
        padding: 0 22rem 5rem;
      }

      @include xl {
        padding: 0 28.9rem 5rem;
      }

      @include breakpoint(1700px) {
        padding: 0 36rem 5rem;
      }
    }
  }
  #section-marker-2 {
    @include section-marker;
    margin: 0 0 -5.75rem 0;
  }
}

footer {
  #footer-container {
    background: linear-gradient(
        to top,
        rgba(77, 150, 169, 0.9),
        rgba(77, 150, 169, 0.9)
      ),
      url("../assets/mobile/image-footer.jpg");
    background-size: cover;
    background-position: 40% 20%;
    background-repeat: no-repeat;
    width: 100%;

    #footer-text {
      @include card-text;
      color: $white;
      padding: 5.75rem 1.5rem 0;

      @include lg {
        flex-direction: row;
        padding: 5rem 8rem 4rem;
        justify-content: space-between;
      }

      @include xl {
        padding: 7.25rem 10.313rem 6.5rem;
      }

      p:nth-child(1) {
        font-size: 2rem;
        line-height: 2.25rem;
        font-weight: 900;
        text-align: center;

        @include md {
          font-size: 2.5rem;
          line-height: 2.75rem;
          padding: 0 6.5rem;
        }

        @include lg {
          text-align: left;
          width: 22rem;
          padding: 0;
        }

        @include xl {
          width: 24rem;
        }
      }

      p:nth-child(2) {
        font-size: 1.125rem;
        line-height: 1.625rem;
        font-weight: 500;
        text-align: center;
        padding: 1.5rem 0 2rem;

        @include md {
          padding: 2rem 4rem 2.5rem;
        }

        @include lg {
          text-align: left;
          width: 22rem;
          padding: 0 2rem 0 0;
          margin: 0 -0.5rem 0 0;
        }

        @include xl {
          width: 24rem;
          padding: 0 2.5rem 0 0;
          margin: 0 -1rem 0 0;
        }
      }

      button {
        @include btn-style;
        width: 12.063rem;
        color: $white;
        background: $dark-purple;
        margin: 0 0 4.5rem;

        &:hover {
          background-color: $medium-purple;
        }

        @include lg {
          margin: 0;
        }

        span {
          color: $light-purple;
        }
      }
    }
  }
}
